@import "../../../styles/_const.scss";

//-----------------------------Left And  Right Sections
.ecommerce-success-stories-box {
  background-color: #efefef;
}
.ecommerce-success-stories-title {
  font-size: $main-fontSize-for-section-title;
  text-align: center;
  font-weight: bold;
  padding: 95px 0 100px 0;
  color: $main-title-and-text-color;
}

//-----------------------------Left And  Right Sections     Box
.ecommerce-success-stories-box-box {
  max-width: 100%;
  max-height: 100%;
  margin-bottom: 100px;
}

.ecommerce-success-stories-box-img {
  width: 100vw;
}
.ecommerce-success-stories-box-info-box {
  display: grid;
  width: 100%;
}

//-----------------------------Left And  Right Sections     Buttons

.ecommerce-success-stories-button-box {
  display: grid;
  grid-template-columns: auto;
  justify-content: center;
  color: $main-title-and-text-color;
  padding-bottom: 100px;
}

.ecommerce-success-stories-button-text {
  border: 3px solid #1c1b29;
  margin-top: 100px;
  border-radius: 30px;
  padding: 10px 55px;
  font-weight: bold;
  font-size: $main-fontSize-for-text;
}

//-----------------------------Left And  Right Sections      Sub box

.ecommerce-success-stories-sub-box-box {
  padding-top: 10%;
  padding-left: 10%;
  padding-right: 10%;
  width: 100%;
  background: $main-white-color;
  box-shadow: $main-box-shadow;
}
.left-right-box:nth-child(even) {
  // need check
  background-color: $main-white-color;
  width: 57%;
  height: 100%;
  margin-top: -26%;
}
.left-right-box:nth-child(odd) {
  // need check
  background-color: $main-white-color;
  width: 57%;
  height: 100%;
  margin-top: -26%;
}

.ecommerce-success-stories-box-box:nth-child(even) .left-right-box {
  justify-self: end;
}

@media only screen and (min-width: 2200px) {
  .left-right-box {
    height: 110%;
  }
}
@media only screen and (min-width: 2300px) {
  .left-right-box {
    height: 120%;
  }
}

@media only screen and (max-width: 1806px) {
  .left-right-box {
    height: 70%;
  }
}

//-----------------------------Left And  Right Sections      Sub box Buttons

.ecommerce-success-stories-sub-box-button {
  border-radius: 30px;
  border: 3px solid $main-title-and-text-color;
  padding: 12px 55px;
  background-color: $main-white-color;
  font-size: $main-fontSize-for-text;
  font-weight: bold;
  margin-bottom: 70px;
}

@media only screen and (max-width: 1366px) {
  .ecommerce-success-stories-sub-box-button {
    padding: 10px 40px;
    font-size: 18px;
  }
}

//-----------------------------Left And  Right Sections      Sub box Text
.commerce-success-stories-sub-box-title {
  font-weight: bold;
  font-size: $main-fontSize-for-section-title;
  color: $main-title-and-text-color;
  margin-bottom: 46px;
}

.commerce-success-stories-sub-box-Text {
  font-size: $main-fontSize-for-text-Ecommerce;
  color: $main-title-and-text-color;
  margin-bottom: 51px;
}

@media only screen and (max-width: 1650px) {
  .commerce-success-stories-sub-box-title {
    font-size: 40px;
  }
  .commerce-success-stories-sub-box-Text {
    font-size: 20px;
  }
}
@media only screen and (max-width: 1366px) {
  .commerce-success-stories-sub-box-Text {
    font-size: 18px;
  }
}

//------------------------------------------------------------------------------------------MOBILE VERSION

@media only screen and (max-width: 600px) and (min-width: 300px) {
  .ecommerce-success-stories-title {
    font-size: 25px;
    text-align: center;
    font-weight: bold;
    padding: 46px 0 51px 0;
    color: $main-title-and-text-color;
  }
  .ecommerce-success-stories-box-img {
    width: 100vw;
    object-fit: cover;
    height: 300px;
  }
  .ecommerce-success-stories-sub-box-box {
    padding-top: 0px;
    padding-left: 20px;
    padding-right: 20px;
    width: 100%;
    background: $main-white-color;
    box-shadow: $main-box-shadow;
  }
  .left-right-box:nth-child(even) {
    // need check
    background-color: $main-white-color;
    width: 90%;
    height: 100%;
    margin-left: 5%;
    margin-top: -40px;
  }
  .left-right-box:nth-child(odd) {
    // need check
    background-color: $main-white-color;
    width: 90%;
    height: 100%;
    margin-left: 5%;
    margin-top: -40px;
  }
  .ecommerce-success-stories-box-box:nth-child(even) .left-right-box {
    justify-self: start;
  }

  .commerce-success-stories-sub-box-title {
    font-weight: bold;
    font-size: 20px;
    color: $main-title-and-text-color;
    padding: 32px 0 19px 0;
    margin-bottom: 0;
  }
  .commerce-success-stories-sub-box-Text {
    font-size: 14px;
    color: $main-title-and-text-color;
    margin-bottom: 35px;
  }
  .ecommerce-success-stories-button-text {
    border: 3px solid #1c1b29;
    margin-top: 0px;
    border-radius: 30px;
    padding: 10px 55px;
    font-weight: bold;
    font-size: 14px;
  }
  .ecommerce-success-stories-button-box {
    display: grid;
    grid-template-columns: auto;
    justify-content: center;
    color: $main-title-and-text-color;
    padding-bottom: 50px;
    margin-top: -50px;
  }

  .ecommerce-success-stories-button-text {
    border: 3px solid #1c1b29;

    border-radius: 30px;
    padding: 10px 55px;
    font-weight: bold;
    font-size: 14px;
  }

  .ecommerce-success-stories-sub-box-button {
    border-radius: 30px;
    border: 3px solid $main-title-and-text-color;
    padding: 7px 35px;
    background-color: $main-white-color;
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 35px;
  }
}
